import React from 'react'
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import TelegramIcon from '@mui/icons-material/Telegram';


const BlogsLeftCard = ({ Blogcattitle, Image, Heading, Paragraph, Btn, Admin, Likes, Comments }) => {
    return (
        <>
            <div className="row blog-row-cols mb-3">
                <div className="col-lg-6 col-md-6 inner">
                    <img src={Image} className='img-fluid' alt="" />
                </div>
                <div className="col-lg-6 col-md-6">
                    <div className=''>
                        <h6 className='blogcalenderdt'>{Blogcattitle}</h6>
                        <h5 className='mb-3'>{Heading}</h5>
                        <ul className='d-flex p-0'>
                            <li>
                                {Admin}
                            </li>
                            <li className='ms-3'>
                                <FavoriteBorderIcon className='fs-5 text-danger'/>{Likes}
                            </li>
                            <li className='ms-3'>
                               <TelegramIcon className='fs-5 text-info'/> {Comments}
                                
                            </li>
                        </ul>
                        <p className='text-paragraph '>{Paragraph}</p>
                        <div>
                            <a href="" className='btn'>{Btn}</a>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default BlogsLeftCard
