import axios from "axios";
import reducer from "../Reducer/ProductReducer";
import { useState } from "react";
import { createContext, useContext, useEffect, useReducer } from "react";

const AppContext = createContext();
const API = "http://localhost:3001/cardata";
const initialState = {
    isLoading: false,
    isError: false,
    products: [],
    allproduct:[],
    carts:[],
    featureProducts: [],
    isSingleLoading: false,
    singleProduct: {},
};

const AppProvider = ({ children }) => {
    const [quantity, setQuantity] = useState(1);
    const [totalPrice, setTotalPrice] = useState(0);

    const [state, dispatch] = useReducer(reducer, initialState);


    const getProduct = async (url) => {
        dispatch({ type: "SET_LOADING" });
        try {
            const res = await axios.get(url);
            const products = await res.data;
            dispatch({ type: "SET_API_DATA", payload: products });
        } catch (error) {
            dispatch({ type: "API_ERROR" });
        }
    };


    const getSingleProduct = async (url) => {
        dispatch({ type: "SET_SINGLE_LOADING" });
        try {
            const res = await axios.get(url);
            const singleProduct = await res.data;
            dispatch({ type: "SET_SINGLE_PRODUCT", payload: singleProduct });
        } catch (error) {
            dispatch({ type: "SET_SINGLE_ERROR" });
        }
    };



    const handleCart = (singleProduct, quantity, totalPrice) =>{
        dispatch({ type: "SET_ADD_TO_CART_PRODUCT", payload: {product: singleProduct[0], quantity, totalPrice}});
    }


    const handleCategoryFilter = (Category) => {
        dispatch({ type: 'FILTER_BY_CATEGORY', payload: Category });
    };

 

    let base_url = process.env.REACT_APP_BASE_URL;
    let public_url = process.env.PUBLIC_URL;

    useEffect(() => {
        getProduct(API);
    }, []);


    return (
        <AppContext.Provider value={{ ...state, getSingleProduct,  handleCart,handleCategoryFilter, setTotalPrice ,  base_url, public_url }}>
            {children}
        </AppContext.Provider>
    );
};

const useProductContext = () => {
    return useContext(AppContext);
};

export { AppProvider, AppContext, useProductContext };