import React from 'react'
import { Container } from 'react-bootstrap-v5'
import LocalMallRoundedIcon from '@mui/icons-material/LocalMallRounded';
import AccountCircleRoundedIcon from '@mui/icons-material/AccountCircleRounded';
import ShoppingCartRoundedIcon from '@mui/icons-material/ShoppingCartRounded';
const HeaderTopNav = () => {
    return (
        <>
            <section className='HeaderTopNav'>
                <Container>
                    <div className='text-center d-flex align-items-center justify-content-between'>
                        <h6>+Users</h6>
                        <h6>Introductory Offer Get Upto <strong>40% Off</strong> Buy Now</h6>
                        <div>
                            <AccountCircleRoundedIcon/>
                            <LocalMallRoundedIcon className='ms-3'/>
                            <ShoppingCartRoundedIcon className='ms-3'/>
                        </div>
                    </div>
                </Container>
            </section>
        </>
    )
}

export default HeaderTopNav
