import React, { useState } from 'react'
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import './css/Home.css';
import { Container } from 'react-bootstrap-v5';

const HomeSection3 = () => {

    let time = new Date().toLocaleTimeString();

    const [ctime, setCtime] = useState(time);

    const UpdateTime = () => {
        time = new Date().toLocaleTimeString();
        setCtime(time);
    };
    setInterval(UpdateTime, 1000)
    return (
        <>
            <section className="Limited-offers-section mt-5 mb-5">
                <Container>

                    <div className="">
                        <div className='text-center text-white'>
                            <h3 className='mb-4'>End of Season Clearance
                                <span className='d-md-block fs-2 text-white'>Sale upto 30%</span></h3>
                            <a href="/" className='btn'> <AddShoppingCartIcon className='me-1' />Shop Now</a>


                            <ul className='d-flex justify-content-center Limited-offers-timmer '>
                                <li>
                                    <span className='mb-0 pb-0'>09</span>
                                    <h6 className='d-block fs-5'>Days</h6>
                                </li>
                                <li>
                                    <span className='mb-0 pb-0'>13</span>
                                    <h6 className='d-block fs-5'>Hours</h6>
                                </li>
                                <li>
                                    <span className='mb-0 pb-0'>17</span>
                                    <h6 className='d-block fs-5'>Min</h6>
                                </li>
                                <li>
                                    <span className='mb-0 pb-0'>01</span>
                                    <h6 className='d-block fs-5'>Sec</h6>
                                </li>
                            </ul>
                        </div>
                    </div>

                </Container>

            </section>
        </>
    )
}

export default HomeSection3
